import React from "react";
import { useSelector } from "react-redux";
import { Link, useHistory } from "react-router-dom";
import CustomTitle from "../layouts/CustomTitle";
import CheckoutSteps from "./CheckoutSteps";

export default function ConfirmOrder() {
  const history = useHistory();
  // global state
  const { user } = useSelector((state) => state.user);
  const { cartItems, shippingInfo } = useSelector((state) => state.cart);

  // Calculate Order Prices
  const itemsPrice = cartItems.reduce((acc, item) => acc + item.price * item.quantity, 0);
  const shippingPrice = itemsPrice > 200 ? 0 : 25;
  const taxPrice = Number((0.05 * itemsPrice).toFixed(2));
  const totalPrice = (itemsPrice + shippingPrice + taxPrice).toFixed(2);

  // handler
  const processToPayment = () => {
    const data = {
      itemsPrice: itemsPrice.toFixed(2),
      shippingPrice,
      taxPrice,
      totalPrice,
    };

    sessionStorage.setItem("orderInfo", JSON.stringify(data));
    history.push("/payment");
  };

  return (
    <>
      <CustomTitle title="Confirm Order" />
      <CheckoutSteps shipping confirmOrder />
      <div className="container container-fluid ">
        <div className="row d-flex justify-content-between">
          <div className="col-12 col-lg-8 mt-5 order-confirm">
            <h4 className="mb-3">Shipping Info</h4>
            <p>
              <b>Name: </b>
              {user && user.name}
            </p>
            <p>
              <b>Phone: </b> {shippingInfo.phoneNo}
            </p>
            <p className="mb-4">
              <b>Address: </b>
              {shippingInfo.address}, {shippingInfo.city},{shippingInfo.postalCode},{" "}
              {shippingInfo.country}
            </p>
            <hr />
            <h4 className="mt-4">Your Cart Items:</h4>

            {cartItems.map((i) => (
              <div className="cart-item my-1" key={i.productId}>
                <hr />
                <div className="row">
                  <div className="col-4 col-lg-2">
                    <img src={i.image} alt="Laptop" height="45" width="65" />
                  </div>

                  <div className="col-5 col-lg-6">
                    <Link to={`/product/${i.productId}`}>{i.name}</Link>
                  </div>

                  <div className="col-4 col-lg-4 mt-4 mt-lg-0">
                    <p>
                      {`${i.quantity} x ${i.price}`} =
                      <b> ${Number(i.quantity * i.price).toFixed(2)}</b>
                    </p>
                  </div>
                </div>
              </div>
            ))}
            <hr />
          </div>

          <div className="col-12 col-lg-3 my-4">
            <div id="order_summary">
              <h4>Order Summary</h4>
              <hr />
              <p>
                Subtotal:{" "}
                <span className="order-summary-values">${Number(itemsPrice).toFixed(2)}</span>
              </p>
              <p>
                Shipping:{" "}
                <span className="order-summary-values"> ${Number(shippingPrice).toFixed(2)}</span>
              </p>
              <p>
                Tax / GST:
                <span className="order-summary-values">${Number(taxPrice).toFixed(2)}</span>
              </p>

              <hr />

              <p>
                Total: <span className="order-summary-values">${totalPrice}</span>
              </p>

              <hr />
              <button
                onClick={processToPayment}
                id="checkout_btn"
                className="btn btn-primary btn-block"
              >
                Proceed to Payment
              </button>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}
